<style id="initial-style">
body {
	height: 100vh;
	margin: 0;
	width: 100vw
}

#game-shell {
	display: flex;
	height: 100%;
	position: fixed;
	width: 100%
}

#game-box {
	/* position: absolute; */
	width: 100%;
	height: 100%;
}

#GameCanvas {
	width: 100%;
	height: 100%;
	/* position: absolute; */
}

#game-overlay {
	height: 0;
	position: absolute;
	width: 0
}

#background-img {
	background-size: cover;
	/* bottom: -10%; */
	height: 100%;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
	width: 100%;
	background-image: url("../assets/image/back.jpg");
	background-position: 50%;
}


#background-img_pc {
	background-size: cover;
	/* bottom: -10%; */
	height: 100%;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
	width: 100%;
	background-image: url("../assets/image/backPC.jpg");
	background-position: 50%;
}

#block-page,
#scroll-area {
	height: 100%;
	position: absolute;
	width: 100%
}

#block-page {
	left: 0;
	margin: auto;
	top: 0
}
</style>
<style id="progress-bar-style">
#progress-bar-bg-cover {
	width: 100vw;
	height: 100vh;
	background-color: #000;
	position: fixed;
	z-index: 999;
}



/* 公共进度条背景样式 */
.progress-bar-bg {
	background-color: #4d4900;
	min-height: 5px;
	z-index: 10;
	position: fixed;
}

/* App 端样式 */
.progress-bar-bg.app-style {
	width: 100vh;
	height: 2vw;
	right: 0;
	top: 100%;
	transform: translateY(-50%) rotate(90deg);
	transform-origin: right center;
}

/* Windows 端样式 */
.progress-bar-bg.pc-style {
	width: 100vw;
	height: 0.6vh;
	left: 0;
	top: 0;
}

/* 公共进度条内容 */
#progress-bar-content {
	background: #59e390;
	width: 0%;
	height: 100%;
	min-height: 5px;
}

/* 公共图片样式 */
.progress-bar-image {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 9;
}

/* App 专属旋转样式 */
.progress-bar-image.app-style {
	transform: translate(-50%, -50%) rotate(90deg);
}



@media all and (orientation : portrait) {
	.progress-bar-image {
		width: 100vw;
		height: 100vw;
	}
}
</style>

<style id="loader-style">
#initial-loader-1 {
	background-color: #000;
	margin: auto;
	position: fixed;
	width: 100vw;
	z-index: 98;
	height: 100vh;
	top: 0;
	left: 0;
}

/* 通用 */
.initial-loader-img {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translateY(-50%) translateX(-50%);
}

.app-style {
	transform: translateY(-50%) translateX(-50%) rotate(90deg);
}

@media all and (orientation: portrait) {
	.initial-loader-img {
		width: 100vw;
		height: 100vw;
	}
}








#initial-loader-1,
.svg-loading {
	align-items: center;
	display: flex;
	flex-direction: column;
	justify-content: center
}

.circle-loading {
	align-items: center;
	display: flex;
	height: 10px;
	justify-content: space-between;
	width: 40px
}

.hide-loading {
	animation-duration: .35s;
	animation-fill-mode: forwards;
	animation-name: fade-out;
	animation-timing-function: ease-in
}

.loader-circle {
	animation-direction: alternate;
	animation-duration: .25s;
	animation-iteration-count: infinite;
	animation-name: loader-circle-bounce;
	animation-timing-function: ease-out;
	background-color: #30a2d0;
	border-radius: 50%;
	height: 10px;
	position: relative;
	width: 10px
}

.loader-circle:first-of-type {
	animation-delay: 0s
}

.loader-circle:nth-of-type(2) {
	animation-delay: -75ms
}

.loader-circle:nth-of-type(3) {
	animation-delay: -.15s
}

@keyframes loader-circle-bounce {
	0% {
		bottom: 0
	}

	90%,
	to {
		bottom: 15px
	}
}

@keyframes fade-out {
	to {
		opacity: 0
	}
}

@keyframes fade-in {
	to {
		opacity: 1
	}
}
</style>
