					<img v-if="DeviceType == 1" id="footer-copyright-image-id" class="footer-copyright-image"
						:src="$i18n.locale == 'pt' ? require('@/assets/image/pt/website.png') : require('@/assets/image/en/website.png')"
						style="height: 8px; display: block; position: absolute; bottom: 0px; left: 8%; transform: scale(1.6)">
					</img>

					<div v-if="DeviceType == 2" style="position: relative; height: 10px;">
						<div style="position: absolute; left: 50%; bottom: -20px; transform: translateX(-50%);">
							<img class="footer-text-img" :src="$i18n.locale === 'pt'
								? require('@/assets/image/pt/copyright.png')
								: require('@/assets/image/en/copyright.png')"
								style="height: 50px;width: auto; display: block;align-items: center;" />
							<img id="footer-copyright-image-id" class="footer-copyright-image" :src="$i18n.locale === 'pt'
								? require('@/assets/image/pt/website.png')
								: require('@/assets/image/en/website.png')"
								style="height: 8px; display: block; margin-top: 2px; margin-left: auto; margin-right: auto;" />
						</div>
					</div>

				</div>
			</div>
		</div>
		<div id="initial-loader-1">
			<img :class="initialloaderimg" src="../assets/image/循環.webp" alt="" srcset="" />
		</div>
	</div>
</template>

<script>
export default {
	computed: {
		splashStyle() {
			if (this.DeviceType == 2) {
				return {
					position: 'absolute',
					top: `5%`,
					left: 0,
					height: `${this.configurationHeight}px`,
					width: `780px`,
					backgroundRepeat: 'no-repeat',
					backgroundPosition: 'center',
					backgroundSize: 'cover'
				}
			} else {
				return {
					position: 'absolute',
					top: 0,
					left: 0,
					width: '360px',
					height: `${this.configurationHeight}px`,
					backgroundRepeat: 'no-repeat',
					backgroundPosition: 'center',
					backgroundSize: 'cover'
				}
			}

		},
		screenCompat() {
			return this.DeviceType == 2 ? 'screen_color screen_compat rotate-box' : 'screen_color screen_compat_pc'
		},
		initialloaderimg() {
			return this.DeviceType == 2 ? 'initial-loader-img app-style' : 'initial-loader-img'
		},
		progressBarBg() {
			return this.DeviceType == 2 ? 'progress-bar-bg app-style' : 'progress-bar-bg pc-style'
		},
		progressBarImage() {
			return this.DeviceType == 2 ? 'progress-bar-image app-style' : 'progress-bar-image'
		},
		backgroundImg() {
			return this.DeviceType == 2 ? 'background-img' : 'background-img_pc'
		},
		startButtonContainerPort() {
			return this.DeviceType == 2 ? 'start-button-container-port start-button start-button-show-port' : 'start-button-container-port start-button-container-port_pc start-button start-button-show-port'
		},
		animationTipsContainerPort() {
			return this.DeviceType == 2 ? 'animationTipsContainer-port ' : 'animationTipsContainer-port animationTipsContainer-port_pc'
		},
		loadingContainerPort() {
			return this.DeviceType == 2 ? 'loading-container-port' : 'loading-container-port loading-container-port_pc'
		},
		progressBarContainerPort() {
			return this.DeviceType == 2 ? 'progress-bar-container-port' : 'progress-bar-container-port_pc'
		},
	},
	components: {

	},
	data() {
		return {
			gameId: null,
			timeStamp: '00:00:00',
			configurationHeight: '780',
			showHistory: false,
			hideHistory: false,
			showLoading: false,
			animationLoading: true,
			trophiesCoordinateBottom: 120,
			trophiesCoordinateLeft: 360,
			gameInfo: {
				jbicon_pos: "-999,-999",
				name: ""
			},
			isTrophiesIconShow: false,
			animationDuration: 0.75,
			DeviceType: 1, // 1: PC, 2: Mobile
		}
	},


	mounted() {
		this.DeviceType = this.getDeviceType();
		this.updateWindowSize();
		this._resizeHandler = this.updateWindowSize.bind(this);
		window.addEventListener('resize', this._resizeHandler);

		this.DeviceType = this.getDeviceType();
		let nav1 = document.createElement('script')
		nav1.type = 'text/javascript'
		nav1.src = require('@/assets/js/view.js')

		let nav = document.createElement('script')
		nav.type = 'text/javascript'
		nav.src = require('@/assets/js/game_index.js')

		setInterval(this.getCurrentTime, 1000)

		this.gameId = this.getParams("game_id")
		if (this.gameId) {
			this.getGame()
		}

		window.token = this.getParams("token")
		window.gameId = this.gameId

		localStorage.setItem("token", window.token)
		localStorage.setItem("gameId", window.gameId)


		window.toVerifyByOrderNo = this.toVerifyByOrderNo


		this.reloadAnimation();
	},

	beforeDestroy() {
		window.removeEventListener('resize', this._resizeHandler);
	},

	methods: {
		getDeviceType() {
			const ua = navigator.userAgent.toLowerCase();
			if (/android|iphone|ipad|ipod|windows phone/.test(ua)) {
				return 2;
			}
			return 1;
		},
		reloadAnimation() {
			const img = this.$refs.animatedGif;
			const originalSrc = img.src;

			// 清空src再重新设置
			img.src = 'data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==';

			// 使用setTimeout确保重载生效
			setTimeout(() => {
				img.src = `${originalSrc}?t=${Date.now()}`;
			}, 50);
		},
		updateWindowSize() {
			let finalWidth = 0,
				finalHeight = 0;

			const width = window.innerWidth;
			const height = window.innerHeight;
			const curRatio = height / width;
			const minRatio = 1638 / 756;
			const maxRatio = 1638 / 756;
			if (curRatio < minRatio) {
				finalHeight = height;
				finalWidth = height / minRatio;
			} else if (curRatio > maxRatio) {
				finalWidth = width;
				finalHeight = width * maxRatio;
			} else {
				finalHeight = height;
				finalWidth = width;
			}

			this.configurationHeight = `${(finalHeight / finalWidth) * 360}`
		},
		toVerifyByOrderNo(orderNo) {
			localStorage.setItem("orderNo", orderNo)
			let routeData = this.$router.resolve({
				name: "verify",
			});
			let uuid = this.guid().toUpperCase()
			let link =
				`https://verify.pgsoft.game/?atk=${uuid}&gid=68&env=13&l=pt&sid=1837027255981835264&tid=RCBQTF23&token=${token}&game_id=${window.gameId}&order_no=${orderNo}`
			window.open(link + routeData.href, '_blank');
		},
		guid() {
			return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
				var r = Math.random() * 16 | 0,
					v = c == 'x' ? r : (r & 0x3 | 0x8);
				return v.toString(16);
			});
		},
		changeLoading() {
			if (this.showLoading) {
				this.animationLoading = false
			}
			this.showLoading = !this.showLoading
		},
		getCurrentTime() {
			var _this = this;
			let hh = new Date().getHours();
			let mf = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes();
			let ss = new Date().getSeconds() < 10 ? '0' + new Date().getSeconds() : new Date().getSeconds();
			_this.timeStamp = hh + ':' + mf + ':' + ss;
		},
		getGame() {
			this.$http.get('/api/game/get_config', {
				params: {
					game_id: this.gameId
				}
			}).then(response => {
				if (response.data.meta.code == 0) {
					this.gameInfo = response.data.data
					if (this.gameInfo.jbicon_pos.length) {
						let trophiesCoordinate = this.gameInfo.jbicon_pos.split(",")
						this.trophiesCoordinateBottom = trophiesCoordinate[1]
						this.trophiesCoordinateLeft = trophiesCoordinate[0]
					}
					window.sessionStorage.setItem("trophiesCoordinateLeft", this.trophiesCoordinateLeft);
					window.sessionStorage.setItem("trophiesCoordinateBottom", this.trophiesCoordinateBottom);
				}
			});
		},
		getParams(name) {
			var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
			var r = decodeURI(window.location.search).substr(1).match(reg);
			if (r != null) {
				return unescape(r[2]);
			} else {
				return null;
			}
		},
	}
}
</script>